b.shock b {
  width: 55%;
  height: 100%;
  position: relative;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  margin: 0 auto;
  border-style: solid;
  border-color: #999;
  border-width: 3px 1px;
  border-radius: 10%;
  -webkit-animation: shock 1s infinite;
  animation: shock 1s infinite;
}
b.shock b i {
  border-radius: 20%;
  background-color: #999;
  width: 2px;
  height: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
b.shock b i:nth-child(1) {
  left: -35%;
}
b.shock b i:nth-child(2) {
  right: -35%;
}
b.shock b i::before {
  content: '';
  border-radius: 10%;
  width: 2px;
  height: 60%;
  background-color: #999;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
b.shock b i:nth-child(1)::before {
  left: -200%;
}
b.shock b i:nth-child(2)::before {
  right: -200%;
}
